export class ShippingPolicyViewController extends BaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#shipping_policy_view_container',

			type_array: null,
			weight_unit_array: null
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'ShippingPolicyEditView.html';
		this.permission_id = 'shipping_policy';
		this.viewId = 'ShippingPolicy';
		this.script_name = 'ShippingPolicyView';
		this.table_name_key = 'shipping_policy';
		this.context_menu_name = $.i18n._( 'Shipping' );
		this.navigation_label = $.i18n._( 'Shipping' ) + ':';
		this.api = TTAPI.APIShippingPolicy;

		this.render();
		this.buildContextMenu();

		this.initData();
		this.setSelectRibbonMenuIfNecessary();
	}

	initOptions( callBack ) {
		var $this = this;
		var options = [
			{ option_name: 'type', field_name: null, api: null },
			{ option_name: 'weight_unit', field_name: null, api: null }
		];

		this.initDropDownOptions( options, function( result ) {

			if ( callBack ) {
				callBack( result ); // First to initialize drop down options, and then to initialize edit view UI.
			}

		} );
	}

	buildEditViewUI() {

		super.buildEditViewUI();

		var $this = this;

		var tab_model = {
			'tab_shipping': { 'label': $.i18n._( 'Shipping' ) },
			'tab_carrier_rate_server': { 'label': $.i18n._( 'Carrier Rate Server' ) },
			'tab_area_criteria': { 'label': $.i18n._( 'Area Criteria' ) },
			'tab_audit': true,
		};
		this.setTabModel( tab_model );

		this.navigation.AComboBox( {
			api_class: TTAPI.APIShippingPolicy,
			id: this.script_name + '_navigation',
			allow_multiple_selection: false,
			layout_name: 'global_shipping_policy',
			navigation_mode: true,
			show_search_inputs: true
		} );

		this.setNavigation();

		//Tab 0 start

		var tab_shipping = this.edit_view_tab.find( '#tab_shipping' );

		var tab_shipping_column1 = tab_shipping.find( '.first-column' );

		this.edit_view_tabs[0] = [];

		this.edit_view_tabs[0].push( tab_shipping_column1 );

		var form_item_input;
		var widgetContainer;
		var label;

		//Type
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'type_id', set_empty: false } );
		form_item_input.setSourceData( $this.type_array );
		this.addEditFieldToColumn( $.i18n._( 'Type' ), form_item_input, tab_shipping_column1, '' );

		// Currency

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APICurrency,
			allow_multiple_selection: false,
			layout_name: 'global_currency',
			show_search_inputs: true,
			set_empty: true,
			field: 'currency_id'
		} );
		this.addEditFieldToColumn( $.i18n._( 'Currency' ), form_item_input, tab_shipping_column1, '' );

		// Name

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'name', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Name' ), form_item_input, tab_shipping_column1 );
		form_item_input.parent().width( '45%' );

		// Base Price

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'base_price', width: 100 } );
		this.addEditFieldToColumn( $.i18n._( 'Base Price' ), form_item_input, tab_shipping_column1 );

		// Price

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'price', width: 100 } );
		this.addEditFieldToColumn( $.i18n._( 'Price' ), form_item_input, tab_shipping_column1 );

		// Minimum Price
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'minimum_price', width: 100 } );

		widgetContainer = $( '<div class=\'widget-h-box\'></div>' );
		label = $( '<span class=\'widget-right-label\'>' + '(' + $.i18n._( 'Not including handing fee' ) + ')' + '</span>' );

		widgetContainer.append( form_item_input );
		widgetContainer.append( label );
		this.addEditFieldToColumn( $.i18n._( 'Minimum Price' ), form_item_input, tab_shipping_column1, '', widgetContainer, true );

		// Maximum Price
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'maximum_price', width: 100 } );

		widgetContainer = $( '<div class=\'widget-h-box\'></div>' );
		label = $( '<span class=\'widget-right-label\'>' + '(' + $.i18n._( 'Not including handing fee' ) + ')' + '</span>' );

		widgetContainer.append( form_item_input );
		widgetContainer.append( label );
		this.addEditFieldToColumn( $.i18n._( 'Maximum Price' ), form_item_input, tab_shipping_column1, '', widgetContainer, true );

		// Price

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'handling_fee', width: 100 } );
		this.addEditFieldToColumn( $.i18n._( 'Handing Fee' ), form_item_input, tab_shipping_column1 );

		// Display Order

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'display_order', width: 50 } );
		this.addEditFieldToColumn( $.i18n._( 'Display Order' ), form_item_input, tab_shipping_column1 );

		//Weight Unit

		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'weight_unit_id' } );
		form_item_input.setSourceData( $this.weight_unit_array );
		this.addEditFieldToColumn( $.i18n._( 'Weight Unit' ), form_item_input, tab_shipping_column1, '' );

		//Product

		var args = { filter_data: {} };
		args.filter_data.type_id = 60;

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIProduct,
			layout_name: 'global_product',
			show_search_inputs: true,
			set_empty: true,
			field: 'product_id'
		} );
		form_item_input.setDefaultArgs( args );
		this.addEditFieldToColumn( $.i18n._( 'Product' ), form_item_input, tab_shipping_column1, '' );

		//Tab 1 start

		var tab_carrier_rate_server = this.edit_view_tab.find( '#tab_carrier_rate_server' );

		var tab_carrier_rate_server_column1 = tab_carrier_rate_server.find( '.first-column' );

		this.edit_view_tabs[1] = [];

		this.edit_view_tabs[1].push( tab_carrier_rate_server_column1 );

		// Name

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'user_name' } );
		this.addEditFieldToColumn( $.i18n._( 'User Name' ), form_item_input, tab_carrier_rate_server_column1, '' );

		// Password

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'password' } );
		this.addEditFieldToColumn( $.i18n._( 'Password' ), form_item_input, tab_carrier_rate_server_column1, '' );

		// Primary Account

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'primary_account' } );
		this.addEditFieldToColumn( $.i18n._( 'Primary Account' ), form_item_input, tab_carrier_rate_server_column1, '' );

		// Secondary Account

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'secondary_account' } );
		this.addEditFieldToColumn( $.i18n._( 'Secondary Account' ), form_item_input, tab_carrier_rate_server_column1, '' );

		// Markup Percent
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'markup_percent', width: 50 } );

		widgetContainer = $( '<div class=\'widget-h-box\'></div>' );
		label = $( '<span class=\'widget-right-label\'>%</span>' );

		widgetContainer.append( form_item_input );
		widgetContainer.append( label );
		this.addEditFieldToColumn( $.i18n._( 'Markup Percent' ), form_item_input, tab_carrier_rate_server_column1, '', widgetContainer, true );

		//Tab 2 start

		var tab_area_criteria = this.edit_view_tab.find( '#tab_area_criteria' );

		var tab_area_criteria_column1 = tab_area_criteria.find( '.first-column' );

		this.edit_view_tabs[2] = [];

		this.edit_view_tabs[2].push( tab_area_criteria_column1 );

		//Include Area Policy

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIAreaPolicy,
			allow_multiple_selection: true,
			layout_name: 'global_area_policy',
			show_search_inputs: true,
			set_empty: true,
			field: 'include_area_policy'
		} );
		this.addEditFieldToColumn( $.i18n._( 'Include Area Policy' ), form_item_input, tab_area_criteria_column1, '' );

		//Exclude Area Policy

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIAreaPolicy,
			allow_multiple_selection: true,
			layout_name: 'global_area_policy',
			show_search_inputs: true,
			set_empty: true,
			field: 'exclude_area_policy'
		} );
		this.addEditFieldToColumn( $.i18n._( 'Exclude Area Policy' ), form_item_input, tab_area_criteria_column1, '' );
	}

	buildSearchFields() {

		super.buildSearchFields();
		this.search_fields = [

			new SearchField( {
				label: $.i18n._( 'Name' ),
				in_column: 1,
				field: 'name',
				multiple: true,
				basic_search: true,
				form_item_type: FormItemType.TEXT_INPUT
			} ),

			new SearchField( {
				label: $.i18n._( 'Type' ),
				in_column: 1,
				field: 'type_id',
				multiple: true,
				basic_search: true,
				layout_name: 'global_option_column',
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Created By' ),
				in_column: 2,
				field: 'created_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Updated By' ),
				in_column: 2,
				field: 'updated_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.AWESOME_BOX
			} )
		];
	}

}